import { CalculationResults, formatCurrency, formatRange } from '@/lib/calculations';

interface ScenarioCardProps {
  title: string;
  results: CalculationResults;
  showRaisedFeeAdvice?: boolean;
  onEmailClick: () => void;
}

export function ScenarioCard({ title, results, showRaisedFeeAdvice = false, onEmailClick }: ScenarioCardProps) {
  const sessionPercentage = Math.min((results.sessionHours / results.capacityTarget) * 100, 100);
  const docPercentage = Math.min((results.docHours / results.capacityTarget) * 100, 100 - sessionPercentage);
  const adminPercentage = Math.min((results.adminHours / results.capacityTarget) * 100, 100 - sessionPercentage - docPercentage);

  return (
    <div className="bg-nesso-card rounded-2xl ring-1 ring-black/5 shadow-sm p-6 space-y-6">
      {/* Title */}
      <h3 className="text-xl font-semibold text-nesso-navy">{title}</h3>

      {/* Metrics */}
      <div className="space-y-4">
        <div>
          <div className="text-sm text-nesso-ink/60 mb-1">Caseload needed</div>
          <div className="text-2xl font-semibold text-nesso-navy">
            {formatRange(results.caseloadRange.low, results.caseloadRange.high)} clients/week
          </div>
        </div>

        <div>
          <div className="text-sm text-nesso-ink/60 mb-1">Time commitment per week</div>
          <div className="text-xl font-semibold text-nesso-navy">{results.totalHours.toFixed(1)}h</div>
          <div className="text-xs text-nesso-ink/60">
            Sessions {results.sessionHours.toFixed(1)}h • Documentation {results.docHours.toFixed(1)}h • Admin {results.adminHours.toFixed(1)}h
          </div>
        </div>

        <div>
          <div className="text-sm text-nesso-ink/60 mb-1">Monthly revenue</div>
          <div className="text-xl font-semibold text-nesso-navy">{formatCurrency(results.revenueMonthly)}</div>
        </div>
      </div>

      {/* Goal Bar */}
      <div className="space-y-3">
        <div className="relative w-full">
          <div className="relative w-full h-8 overflow-hidden">
            {/* Base track */}
            <div className="absolute inset-0 bg-nesso-navy/10 rounded-md" />

            {/* Progress fill */}
            <div className="relative h-full flex overflow-hidden rounded-md">
              {/* Sessions segment */}
              <div
                className="bg-nesso-coral flex items-center justify-center text-black text-xs font-medium"
                style={{ width: `${(results.sessionHours / results.capacityTarget) * 100}%` }}
                title={`Sessions: ${results.sessionHours.toFixed(1)}h`}
              >
                <span className="hidden md:block text-xs font-medium truncate px-1">
                  {results.sessionHours > 3 && sessionPercentage > 15 ? `Sessions ${results.sessionHours.toFixed(1)}h` : ''}
                </span>
              </div>

              {/* Documentation segment */}
              <div
                className="bg-nesso-purple flex items-center justify-center text-white text-xs font-medium"
                style={{ width: `${(results.docHours / results.capacityTarget) * 100}%` }}
                title={`Documentation: ${results.docHours.toFixed(1)}h`}
              >
                <span className="hidden md:block text-xs font-medium truncate px-1">
                  {results.docHours > 2 && docPercentage > 12 ? `Docs ${results.docHours.toFixed(1)}h` : ''}
                </span>
              </div>

              {/* Admin segment */}
              <div
                className="bg-nesso-navy flex items-center justify-center text-white text-xs font-medium"
                style={{ width: `${(results.adminHours / results.capacityTarget) * 100}%` }}
                title={`Admin: ${results.adminHours.toFixed(1)}h`}
              >
                <span className="hidden md:block text-xs font-medium truncate px-1">
                  {results.adminHours > 2 && adminPercentage > 12 ? `Admin ${results.adminHours.toFixed(1)}h` : ''}
                </span>
              </div>
            </div>

            {/* Overflow indicator */}
            {results.hoursOver > 0 && (
              <div
                className="absolute top-0 bg-red-500 h-full flex items-center justify-center text-white text-xs font-medium rounded-r-md"
                style={{
                  left: '100%',
                  width: `${Math.min((results.hoursOver / results.capacityTarget) * 100, 50)}%`,
                }}
                title={`Over goal by ${results.hoursOver.toFixed(1)}h`}
              >
                <span className="text-xs font-medium px-1">Over</span>
              </div>
            )}
          </div>

          {/* Edge labels */}
          <div className="flex justify-between items-center mt-1">
            <div className="text-xs text-nesso-ink/60">0h</div>
            <div className="text-xs text-nesso-ink/60">Goal: {results.capacityTarget}h</div>
          </div>

          {/* Status message */}
          <div className="mt-2 text-xs text-nesso-ink/60">
            {results.hoursOver > 0 && `${results.hoursOver.toFixed(1)}h over`}
            {results.hoursLeft > 0 && `${results.hoursLeft.toFixed(1)}h available`}
          </div>

          {/* Footnote */}
          <div className="mt-3 text-xs text-nesso-ink/60">
            Includes 20 minutes per client per week for documentation
          </div>
        </div>
      </div>

      {/* Wellness Checks */}
      <div className="space-y-3 pt-3 border-t border-nesso-navy/10">
        <div className="text-sm font-medium text-nesso-navy">Wellness checks</div>
        <div className="space-y-2">
          <div className="flex items-center gap-2">
            <div className={`w-4 h-4 rounded flex items-center justify-center ${results.financialOK ? 'bg-green-100' : 'bg-gray-100'}`}>
              {results.financialOK && (
                <svg className="w-3 h-3 text-green-700" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                </svg>
              )}
            </div>
            <span className="text-sm text-nesso-ink">Financial wellness</span>
          </div>
          <div className="flex items-center gap-2">
            <div className={`w-4 h-4 rounded flex items-center justify-center ${results.timeOK ? 'bg-green-100' : 'bg-gray-100'}`}>
              {results.timeOK && (
                <svg className="w-3 h-3 text-green-700" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                </svg>
              )}
            </div>
            <span className="text-sm text-nesso-ink">Time balance</span>
          </div>
          <div className="flex items-center gap-2">
            <div className={`w-4 h-4 rounded flex items-center justify-center ${results.qualityOK ? 'bg-green-100' : 'bg-gray-100'}`}>
              {results.qualityOK && (
                <svg className="w-3 h-3 text-green-700" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                </svg>
              )}
            </div>
            <span className="text-sm text-nesso-ink">Quality care</span>
          </div>
        </div>

        {/* Overall assessment */}
        <div className="pt-2">
          <div className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium ${
            results.overallLabel === 'sustainable' ? 'bg-blue-100 text-blue-800' :
            results.overallLabel === 'room-to-grow' ? 'bg-green-100 text-green-800' :
            'bg-red-100 text-red-800'
          }`}>
            {results.overallLabel === 'sustainable' && 'Sustainable'}
            {results.overallLabel === 'room-to-grow' && 'Room to Grow'}
            {results.overallLabel === 'challenging' && 'Challenging'}
          </div>
          <p className="mt-2 text-sm text-nesso-ink/80" aria-live="polite">
            {results.overallSnippet}
          </p>
        </div>
      </div>

      {/* Raised fee advice */}
      {showRaisedFeeAdvice && (
        <div className="pt-3 border-t border-nesso-navy/10">
          <div className="bg-nesso-purple/5 rounded-lg p-4 space-y-2">
            <p className="text-sm text-nesso-navy font-medium">About raising fees</p>
            <p className="text-sm text-nesso-ink/80 leading-relaxed">
              We know raising fees can feel uncomfortable. It&apos;s a sensitive conversation with your clients, but it can also be an important step for your sustainability. A few gentle ways to approach it:
            </p>
            <ul className="text-sm text-nesso-ink/80 space-y-1 pl-4 list-disc">
              <li>Start with new clients first—keep existing clients at their current rate until you feel ready.</li>
              <li>When talking with current clients, give them notice well in advance. They&apos;ll appreciate your thoughtfulness: "I will be adjusting my fee in two months. I wanted to give you time to prepare."</li>
              <li>Keep it simple—you don&apos;t need to over-explain. A clear, confident statement usually works best.</li>
            </ul>
          </div>
        </div>
      )}

      {/* CTA */}
      <div className="pt-2">
        <button
          onClick={onEmailClick}
          className="w-full px-4 py-2.5 text-sm rounded-lg bg-nesso-coral hover:bg-nesso-coral/90 text-black font-medium transition-colors focus:ring-2 focus:ring-nesso-coral"
        >
          Email me this plan
        </button>
      </div>
    </div>
  );
}
