'use client';

import { useState, useEffect, useCallback } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { Header } from '@/components/Header';
import { ScenarioCard } from '@/components/ScenarioCard';
import { Label } from '@/components/ui/label';
import { Input } from '@/components/ui/input';
import { calculateCaseload, formatCurrency, formatPercentage, formatRange, getDefaultCapacityTarget } from '@/lib/calculations';

interface PlanState {
  monthlyIncome: number;
  sessionFee: number;
  adminHours: number;
  documentationMinutesPerClient: number;
  cancellationRate: number;
  capacityTarget: number;
}

export default function ReviewPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [planState, setPlanState] = useState<PlanState | null>(null);
  const [isEmailModalOpen, setIsEmailModalOpen] = useState(false);
  const [selectedScenario, setSelectedScenario] = useState<'scenario1' | 'scenario2' | 'all' | null>(null);

  // Initialize from URL params
  useEffect(() => {
    const income = parseFloat(searchParams.get('income') || '0');
    const fee = parseFloat(searchParams.get('fee') || '0');

    if (income > 0 && fee > 0) {
      const capacityTarget = getDefaultCapacityTarget(income);

      setPlanState({
        monthlyIncome: income,
        sessionFee: fee,
        adminHours: 6, // Default
        documentationMinutesPerClient: 20, // Default
        cancellationRate: 0.10, // 10% default
        capacityTarget
      });
    } else {
      // Invalid params, redirect to plan
      router.push('/plan');
    }
  }, [searchParams, router]);

  const handleAdjustGoals = () => {
    if (!planState) return;

    const params = new URLSearchParams({
      income: planState.monthlyIncome.toString(),
      fee: planState.sessionFee.toString()
    });

    router.push(`/plan?${params.toString()}`);
  };

  // Debounced update for assumptions
  const updateAssumption = useCallback((updates: Partial<PlanState>) => {
    setPlanState(prev => prev ? { ...prev, ...updates } : null);
  }, []);

  const handleEmailScenario = (scenario: 'scenario1' | 'scenario2' | 'all') => {
    setSelectedScenario(scenario);
    setIsEmailModalOpen(true);
  };

  if (!planState) {
    return (
      <div className="min-h-screen bg-nesso-bg flex items-center justify-center">
        <div className="flex flex-col items-center space-y-4">
          <div className="w-12 h-12 border-4 border-nesso-peach/20 border-t-nesso-peach rounded-full animate-spin"></div>
          <p className="text-nesso-ink/60">Loading your plan...</p>
        </div>
      </div>
    );
  }

  // Calculate Scenario 1: Your plan (base)
  const scenario1Results = calculateCaseload({
    monthlyIncome: planState.monthlyIncome,
    sessionFee: planState.sessionFee,
    adminHours: planState.adminHours,
    documentationMinutesPerClient: planState.documentationMinutesPerClient,
    cancellationRate: planState.cancellationRate,
    capacityTarget: planState.capacityTarget
  });

  // Calculate Scenario 2: Raised fee (+10%)
  const scenario2Results = calculateCaseload({
    monthlyIncome: planState.monthlyIncome,
    sessionFee: planState.sessionFee * 1.10,
    adminHours: planState.adminHours,
    documentationMinutesPerClient: planState.documentationMinutesPerClient,
    cancellationRate: planState.cancellationRate,
    capacityTarget: planState.capacityTarget
  });

  const capacityLabel = planState.capacityTarget === 40 ? 'Full Time (40h/week)' : 'Part Time (20h/week)';

  return (
    <div className="min-h-screen bg-nesso-bg flex flex-col">
      <Header />

      <main className="flex-1 container mx-auto max-w-7xl px-4 py-12 space-y-12">
        {/* Header */}
        <section className="text-center space-y-4">
          <h1 className="text-3xl md:text-4xl font-semibold tracking-tight text-nesso-navy">
            Your caseload plan
          </h1>
        </section>

        {/* Top Summary Cards */}
        <section className="max-w-4xl mx-auto">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="bg-nesso-card rounded-xl ring-1 ring-black/5 p-6">
              <h3 className="text-sm font-medium text-nesso-ink/60 mb-2">Weekly caseload needed</h3>
              <div className="text-3xl font-semibold text-nesso-navy">
                {formatRange(scenario1Results.caseloadRange.low, scenario1Results.caseloadRange.high)} clients
              </div>
            </div>
            <div className="bg-nesso-card rounded-xl ring-1 ring-black/5 p-6">
              <h3 className="text-sm font-medium text-nesso-ink/60 mb-2">Revenue projection</h3>
              <div className="text-3xl font-semibold text-nesso-navy">
                {formatCurrency(scenario1Results.revenueMonthly)}
              </div>
              <div className="text-sm text-nesso-ink/60">per month</div>
            </div>
          </div>
        </section>

        {/* Assumptions Row */}
        <section className="max-w-4xl mx-auto">
          <div className="bg-nesso-card rounded-xl ring-1 ring-black/5 p-6 space-y-4">
            <div className="flex items-start justify-between gap-4">
              <h3 className="text-base font-semibold text-nesso-navy">Assumptions</h3>
              <button
                onClick={handleAdjustGoals}
                className="px-4 py-2 text-sm rounded-lg border border-nesso-navy/20 text-nesso-navy hover:bg-nesso-navy/5 transition-colors focus:ring-2 focus:ring-nesso-navy/20"
                aria-label="Adjust your goals and return to planning page"
              >
                Adjust goals
              </button>
            </div>

            {/* Read-only summary */}
            <div className="grid grid-cols-2 md:grid-cols-3 gap-3 text-sm">
              <div>
                <span className="text-nesso-ink/60">Income goal:</span>{' '}
                <span className="font-medium text-nesso-navy">{formatCurrency(planState.monthlyIncome)}/mo</span>
              </div>
              <div>
                <span className="text-nesso-ink/60">Session fee:</span>{' '}
                <span className="font-medium text-nesso-navy">{formatCurrency(planState.sessionFee)}</span>
              </div>
              <div>
                <span className="text-nesso-ink/60">Capacity:</span>{' '}
                <span className="font-medium text-nesso-navy">{capacityLabel}</span>
              </div>
            </div>

            {/* Editable assumptions */}
            <div className="pt-4 border-t border-nesso-navy/10">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="adminHours" className="text-sm font-medium text-nesso-navy">
                    Admin time (h/week)
                  </Label>
                  <Input
                    id="adminHours"
                    type="number"
                    min="0"
                    max="20"
                    step="0.5"
                    value={planState.adminHours}
                    onChange={(e) => updateAssumption({ adminHours: parseFloat(e.target.value) || 0 })}
                    className="text-sm"
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="docMinutes" className="text-sm font-medium text-nesso-navy">
                    Documentation (min/client/week)
                  </Label>
                  <Input
                    id="docMinutes"
                    type="number"
                    min="0"
                    max="60"
                    step="5"
                    value={planState.documentationMinutesPerClient}
                    onChange={(e) => updateAssumption({ documentationMinutesPerClient: parseFloat(e.target.value) || 0 })}
                    className="text-sm"
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="cancellationRate" className="text-sm font-medium text-nesso-navy">
                    Cancellation rate (%)
                  </Label>
                  <Input
                    id="cancellationRate"
                    type="number"
                    min="0"
                    max="50"
                    step="1"
                    value={Math.round(planState.cancellationRate * 100)}
                    onChange={(e) => updateAssumption({ cancellationRate: (parseFloat(e.target.value) || 0) / 100 })}
                    className="text-sm"
                  />
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* Scenarios Panel */}
        <section className="max-w-6xl mx-auto">
          <h2 className="text-2xl font-semibold text-nesso-navy mb-6">Scenarios</h2>

          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Scenario 1: Your plan */}
            <ScenarioCard
              title="Your plan"
              results={scenario1Results}
              onEmailClick={() => handleEmailScenario('scenario1')}
            />

            {/* Scenario 2: Raised fee */}
            <ScenarioCard
              title="Raised fee"
              results={scenario2Results}
              showRaisedFeeAdvice={true}
              onEmailClick={() => handleEmailScenario('scenario2')}
            />
          </div>

          {/* Email full overview button */}
          <div className="mt-8 flex justify-center">
            <button
              onClick={() => handleEmailScenario('all')}
              className="px-8 py-3 text-base rounded-xl border-2 border-nesso-coral text-nesso-navy hover:bg-nesso-coral/10 font-medium transition-colors focus:ring-2 focus:ring-nesso-coral"
            >
              Email me the full overview
            </button>
          </div>
        </section>
      </main>

      {/* Email Modal */}
      {isEmailModalOpen && selectedScenario && (
        <EmailCaptureModal
          isOpen={isEmailModalOpen}
          onClose={() => {
            setIsEmailModalOpen(false);
            setSelectedScenario(null);
          }}
          scenario={selectedScenario}
          planState={planState}
          scenario1Results={scenario1Results}
          scenario2Results={scenario2Results}
        />
      )}
    </div>
  );
}

// Email Capture Modal Component
interface EmailCaptureModalProps {
  isOpen: boolean;
  onClose: () => void;
  scenario: 'scenario1' | 'scenario2' | 'all';
  planState: PlanState;
  scenario1Results: any;
  scenario2Results: any;
}

function EmailCaptureModal({ isOpen, onClose, scenario, planState, scenario1Results, scenario2Results }: EmailCaptureModalProps) {
  const [email, setEmail] = useState('');
  const [optIn, setOptIn] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    // Validate email
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      setError('Please enter a valid email address');
      return;
    }

    setIsSubmitting(true);

    // Simulate sending (for now, just collect the email)
    // In the future, this would call an API endpoint
    await new Promise(resolve => setTimeout(resolve, 1000));

    // Store email in localStorage for now (Vercel analytics can track this)
    const emailData = {
      email,
      optIn,
      scenario,
      timestamp: new Date().toISOString(),
      planState
    };

    try {
      const existingEmails = JSON.parse(localStorage.getItem('caseload_emails') || '[]');
      existingEmails.push(emailData);
      localStorage.setItem('caseload_emails', JSON.stringify(existingEmails));
    } catch (err) {
      console.error('Failed to store email:', err);
    }

    setIsSubmitting(false);

    // Show success toast (you can add a toast library later)
    alert('Plan sent. Check your inbox!');
    onClose();
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black/50">
      <div className="bg-white rounded-2xl shadow-xl max-w-md w-full p-6 space-y-4">
        {/* Header */}
        <div className="flex items-start justify-between">
          <div>
            <h2 className="text-xl font-semibold text-nesso-navy">Send your caseload plan to your inbox</h2>
            <p className="text-sm text-nesso-ink/60 mt-1">
              We&apos;ll email your plan summary. You can create a new plan anytime.
            </p>
          </div>
          <button
            onClick={onClose}
            className="text-nesso-ink/60 hover:text-nesso-navy transition-colors"
            aria-label="Close modal"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="email" className="text-sm font-medium text-nesso-navy">
              Email address
            </Label>
            <Input
              id="email"
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="you@example.com"
              required
              className="w-full"
            />
            {error && (
              <p className="text-sm text-red-600">{error}</p>
            )}
          </div>

          <div className="flex items-start gap-2">
            <input
              type="checkbox"
              id="optIn"
              checked={optIn}
              onChange={(e) => setOptIn(e.target.checked)}
              className="mt-1"
            />
            <Label htmlFor="optIn" className="text-sm text-nesso-ink/80 cursor-pointer">
              Send me my plan and keep me in the loop with helpful updates.
            </Label>
          </div>

          <button
            type="submit"
            disabled={isSubmitting}
            className="w-full px-6 py-3 rounded-xl bg-nesso-coral hover:bg-nesso-coral/90 text-black font-medium transition-colors focus:ring-2 focus:ring-nesso-coral disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {isSubmitting ? 'Sending...' : 'Email me my plan'}
          </button>
        </form>
      </div>
    </div>
  );
}
